import Vue from 'vue'
import Router from 'vue-router'
import FB from '@/components/FreeBlock.vue'
import test from '@/components/mytest.vue'
import Seashells from '@/components/Seashells.vue'
import SourceCode from '@/components/SourceCode.vue'
import WriteBook from '@/components/WhiteBook.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: {name: 'Seashells', params: {referredBy: 'NoReferrer'}}
    },
    {
      path: '/:referredBy',
      name: 'Seashells',
      component: Seashells
    },
    {
      path: '/FreeBlock',
      name: 'FreeBlock',
      component: FB
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/SourceCode',
      name: 'SourceCode',
      component: SourceCode
    },
    {
      path: '/WriteBook',
      name: 'WriteBook',
      component: WriteBook
    }
  ]
})



