import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    network: '0', // 当前登录状态 0为非登录 1为主网 其它为测试网络
    networkShort: 'Not Login', // 当前登录状态短提示 分为未登录，主网和测试网络
    PresetNetwork: '4', // 只读 运行所需的预设网络 开发时可以是测试网络4rinkeby 发布时应改为1main主网
    mustNetworkHttp: 'https://rinkeby.infura.io/v3/84101c8a833942018b383e03ca1423f3', // 只读 当没有运行metamask时通过该地址加载只读数据 让程序可以正常显示 发布时需设为主网地址
    tokenName: '',
    tokenSymbol: '',
    totalETH: '',
    totalDividends: '',
    totalToken: '',
    myTokens: '',
    buyPrice: '',
    buyMinimum: '0.02', // 只读 最小买入量
    sellPrice: '',
    sellMinimum: '1', // 只读 最小卖出量
    myDividends: '',
    myReferredLink: '',
    contractAddress: '',
    customerAddress: 'Not Login Metamask',
    referredBy: '',
    officialReferral: 'http://freeblock.luxe/0xBe1bB799f587249ad8bEf492Cc48E0D5bBB2c991'
  },
  getters: {
    getNetwork (state) {
      if (!state.network) {
        state.network = localStorage.getItem('network')
      }
      return state.network
    },
    getNetworkShort (state) {
      if (!state.networkShort) {
        state.networkShort = localStorage.getItem('networkShort')
      }
      return state.networkShort
    },
    getPresetNetwork (state) {
      if (!state.PresetNetwork) {
        state.PresetNetwork = localStorage.getItem('PresetNetwork')
      }
      return state.PresetNetwork
    },
    getMustNetworkHttp (state) {
      if (!state.mustNetworkHttp) {
        state.mustNetworkHttp = localStorage.getItem('mustNetworkHttp')
      }
      return state.mustNetworkHttp
    },
    getTokenName (state) {
      if (!state.tokenName) {
        state.tokenName = localStorage.getItem('tokenName')
      }
      return state.tokenName
    },
    getTokenSymbol (state) {
      if (!state.tokenSymbol) {
        state.tokenSymbol = localStorage.getItem('tokenSymbol')
      }
      return state.tokenSymbol
    },
    getTotalETH (state) {
      if (!state.totalETH) {
        state.totalETH = localStorage.getItem('totalETH')
      }
      return state.totalETH
    },
    getTotalDividends (state) {
      if (!state.totalDividends) {
        state.totalDividends = localStorage.getItem('totalDividends')
      }
      return state.totalDividends
    },
    getTotalToken (state) {
      if (!state.totalToken) {
        state.totalToken = localStorage.getItem('totalToken')
      }
      return state.totalToken
    },
    getBuyPrice (state) {
      if (!state.buyPrice) {
        state.buyPrice = localStorage.getItem('buyPrice')
      }
      return state.buyPrice
    },
    getBuyMinimum (state) {
      if (!state.buyMinimum) {
        state.buyMinimum = localStorage.getItem('buyMinimum')
      }
      return state.buyMinimum
    },
    getSellPrice (state) {
      if (!state.sellPrice) {
        state.sellPrice = localStorage.getItem('sellPrice')
      }
      return state.sellPrice
    },
    getSellMinimum (state) {
      if (!state.sellMinimum) {
        state.sellMinimum = localStorage.getItem('sellMinimum')
      }
      return state.sellMinimum
    },
    getMyTokens (state) {
      if (!state.myTokens) {
        state.myTokens = localStorage.getItem('myTokens')
      }
      return state.myTokens
    },
    getMyDividends (state) {
      if (!state.myDividends) {
        state.myDividends = localStorage.getItem('myDividends')
      }
      return state.myDividends
    },
    getContractAddress (state) {
      if (!state.contractAddress) {
        state.contractAddress = localStorage.getItem('contractAddress')
      }
      return state.contractAddress
    },
    getCustomerAddress (state) {
      if (!state.customerAddress) {
        state.customerAddress = localStorage.getItem('customerAddress')
      }
      return state.customerAddress
    },
    getReferredBy (state) {
      if (!state.referredBy) {
        state.referredBy = localStorage.getItem('referredBy')
      }
      return state.referredBy
    },
    getMyReferredLink (state) {
      if (!state.myReferredLink) {
        state.myReferredLink = localStorage.getItem('myReferredLink')
      }
      return state.myReferredLink
    },
    getOfficialReferral (state) {
      if (!state.officialReferral) {
        state.officialReferral = localStorage.getItem('officialReferral')
      }
      return state.officialReferral
    }
  },
  mutations: {
    setNetwork (state, pm) {
      state.network = pm
    },
    setNetworkShort (state, pm) {
      state.networkShort = pm
    },
    setTotalName (state, pm) {
      state.tokenName = pm
    },
    setTokenSymbol (state, pm) {
      state.tokenSymbol = pm
    },
    setTotalETH (state, pm) {
      state.totalETH = pm
    },
    setTotalDividends (state, pm) {
      state.totalDividends = pm
    },
    setTotalToken (state, pm) {
      state.totalToken = pm
    },
    setMyTokens (state, pm) {
      state.myTokens = pm
    },
    setBuyPrice (state, pm) {
      state.buyPrice = pm
    },
    setSellPrice (state, pm) {
      state.sellPrice = pm
    },
    setMyDividends (state, pm) {
      state.myDividends = pm
    },
    setContractAddress (state, pm) {
      state.contractAddress = pm
    },
    setCustomerAddress (state, pm) {
      state.customerAddress = pm
    },
    setReferredBy (state, pm) {
      state.referredBy = pm
    },
    setMyReferredLink (state, pm) {
      state.myReferredLink = pm
    }

  },
  actions: {
    // /* increment({commit}){
    //    commit("INCREMENT")
    //  }*/
  }
})

export default store



