import seashellsAbi from './seashellsABI'
import store from './store'

const Web3 = require('web3')
const Eth = require('ethjs-query')
const EthContract = require('ethjs-contract')

// let eth = new Eth(new Eth.HttpProvider(store.getters.getMustNetworkHttp))

// const web3 = new web3(new web3.providers.HttpProvider('http://127.0.0.1:8545'))
/* 调用合约 */
const abi = seashellsAbi.abi
const address = seashellsAbi.address
let web3
let seashells

// 检查浏览器
function checkExpress () {
  let ok = 0
  if (navigator.userAgent.indexOf('Firefox') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Opera') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Chrome') >= 0) {
    ok = 1
  }
  console.log('express:' + ok)
  if (ok === 0) {
    if (confirm('以太坊metamask不支持当前浏览器及移动设备' + '\n现在去metamask官网查看详情吗')) {
      location.href = 'https://metamask.io'
    }
  }
  return ok
}

function initContract () {
  if (checkExpress() > 0) { // 当前metamask连接并为预设网络
    if (typeof window.web3 !== 'undefined' && typeof window.web3.currentProvider !== 'undefined') {
      console.log('network is right')
      web3 = new Web3(window.web3.currentProvider)
      let eth = new Eth(window.web3.currentProvider)
      let ethContract = new EthContract(eth)
      // seashells = ethContract(abi).at(address)
      seashells = ethContract(abi).at(address)

      store.commit('setContractAddress', address)
      console.log('contractAddress' + store.getters.getContractAddress)
    } else { // 当没有运行metamask时 通过加载infura获取只读数据 让程序可以正常显示
      console.log('network is wrong & connect: ' + store.getters.getMustNetworkHttp)
      web3 = new Web3(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))
      let eth = new Eth(new Web3.providers.HttpProvider(store.getters.getMustNetworkHttp))

      // web3 = new Web3()
      // web3.setProvider(new Web3.providers.WebsocketProvider('ws://localhost:8546'))

      let ethContract = new EthContract(eth)
      seashells = ethContract(abi).at(address)

      store.commit('setContractAddress', address)
      console.log('contractAddress' + store.getters.getContractAddress)
    }
    getTokenName()
    getTokenSymbol()
    setTimeout(initMetadata(), 1000)
    // 帐号无关的参数 可在此设置 帐号相关参数则在checkMetamask中初始化
    setTimeout(checkMetamask(), 1000 * 5)

    return seashells
  }
}

// 获取常变基本数据不含帐号数据
function initMetadata () {
  totalSupply()
  totalEthereumBalance()
  buyPrice()
  sellPrice()

  // 每20秒更新基础数据,用归递超时调用来模拟间歇调用，禁用setInterval
  setTimeout(function () {
    if (seashells !== null) {
      initMetadata()
    }
  }, 1000 * 20)
}

function getTokenName () {
  seashells.name.call()
    .then((result) => {
      let rest = result[0].toString()
      console.log('getName: ' + rest)
      store.commit('setTotalName', rest)
    })
}

function getTokenSymbol () {
  seashells.symbol.call()
    .then((result) => {
      let rest = result[0].toString()
      store.commit('setTokenSymbol', rest)
    })
}

// 合约以太币总额
function totalEthereumBalance () {
  seashells.totalEthereumBalance()
    .then((result) => {
      let rest = '0'
      if (result[0].toString() > 0) {
        // Math.round(a * 10000) / 10000 保留小数4位
        rest = Math.round(web3.fromWei(result[0].toString()) * 10000) / 10000
      }
      store.commit('setTotalETH', rest)
      store.commit('setTotalDividends', Math.round((rest / 10) * 10000) / 10000)
    })
}

// 民生成token总量
function totalSupply () {
  seashells.totalSupply()
    .then((result) => {
      let rest = '0'
      if (result[0].toString() > 0) {
        rest = Math.round(web3.fromWei(result[0].toString()) * 10000) / 10000
      }
      store.commit('setTotalToken', rest)
    })
}

// 当前地址token数量
function myTokens (fromAddress) {
  seashells.myTokens({from: fromAddress})
    .then((result) => {
      let rest = '0'
      if (result[0].toString() > 0) {
        rest = Math.round(web3.fromWei(result[0].toString()) * 10000) / 10000
        console.log('myTokens: ' + rest)
        if (rest > 100) {
          var referredBy_ = store.getters.getReferredBy
          // 本页地址传值默认为NoReferrer
          // if (referredBy_ === 'NoReferrer' || web3.isAddress(referredBy_)) {
          if (referredBy_ !== '') {
            var myLink = window.location.href.replace(referredBy_, fromAddress)
            store.commit('setMyReferredLink', myLink)
          }
        }
      }
      store.commit('setMyTokens', rest)
    })
}

// 当前地址红利（参数确定是否计算推荐[referre]收入 1:计算，0：不计算）
function myDividends (bool, fromAddress) {
  seashells.myDividends(bool, {from: fromAddress})
    .then((result) => {
      let rest = '0'
      if (result[0].toString() > 0) {
        rest = Math.round(web3.fromWei(result[0].toString()) * 10000) / 10000
        console.log('myDividends: ' + rest)
      }
      store.commit('setMyDividends', rest)
    })
}

// 检索任意地址的红利
function dividendsOf (address) {
  var res = seashells.dividendsOf.call(address)
  return web3.fromWei(res).toFixed(4).toString()
}

// 检索任意地址的代币余额
function balanceOf (_pm) {
  var fromAddress = _pm
  seashells.balanceOf(fromAddress)
    .then((result) => {
      let rest = '0'
      if (result[0].toString() > 0) {
        rest = Math.round(web3.fromWei(result[0].toString()) * 10000) / 10000
      }
      console.log('error' + rest)
    })
}

async function buy (_tokenQty) {
  console.log('************* buy **************')
  // metamask安装检查
  if (typeof window.web3 === 'undefined') {
    if (confirm('您需要先安装以太坊metamask插件才能使用该功能' + '\n现在去metamask官网查看和下载吗？')) {
      location.href = 'https://metamask.io'
    }
    return
  }
  // 检查网络是否为预设网络
  if (store.getters.getNetwork !== store.getters.getPresetNetwork) {
    alert('提醒 您连接的非以太坊主网') // 开发时可以是测试网 发布时需在全局设置中改为主网
    return
  }
  // 检查输入ETH
  if (_tokenQty > 1) {
    alert('out of max [1 eth]')
    return
  }
  // if (_tokenQty < store.getters.getBuyMinimum) {
  if (_tokenQty < 0.001) {
    alert('greater than or equal to 0.02 eth')
    return
  }
  // 检查合约地址
  if (!web3.isAddress(store.getters.getContractAddress)) {
    alert('not get contract address')
    return
  }

  await web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('你还没有登录Metamask')
    } else {
      var buyToken = web3.toWei(_tokenQty, 'ether')
      var referredBy = '0x0000000000000000000000000000000000000000'
      if (store.getters.getReferredBy !== 'NoReferrer' && web3.isAddress(store.getters.getReferredBy)) {
        referredBy = store.getters.getReferredBy
      }
      console.log('\nfrom: ' + myAddress + '\nto: ' + store.getters.getContractAddress + '\nvalue: ' + web3.fromWei(buyToken) + '\nreferredBy: ' + referredBy)

      // 买入事件
      var watchOnTokenPurchase = seashells.onTokenPurchase()
      watchOnTokenPurchase.new()
      watchOnTokenPurchase.watch(function (error, result) {
        if (!error) {
          var res0 = result.args.customerAddress
          var res1 = web3.fromWei(result.args.incomingEthereum)
          var res2 = web3.fromWei(result.args.tokensMinted)
          var res3 = result.args.referredBy
          console.log('Purchase result \ncustomerAddress: ' + res0 + ' \nincomingEthereum: ' + res1 + ' \ntokensMinted: ' + res2 + ' \nreferredBy: ' + res3)
          store.commit('setMyTokens', res2.toString())
          store.commit('setTotalETH', totalEthereumBalance())
        } else {
          myTokens(myAddress)
          myDividends(1, myAddress)
        }
        watchOnTokenPurchase.uninstall()
      })

      // 买入
      seashells.buy(referredBy, {from: myAddress, to: store.getters.getContractAddress, value: buyToken, gas: 210000},
        function (error, result) {
          if (!error) {
            alert('from: ' + myAddress + '\nbuy value: ' + web3.fromWei(buyToken) + '\nhash:' + result + '\nhash has pending please wait')
          } else {
            alert('buy' + error)
          }
        })
    }
  })
}

async function sell (_sellTokens) {
  console.log('************* sell **************')
  // metamask安装检查
  if (typeof web3 === 'undefined') {
    if (confirm('您需要先安装以太坊Metamask插件才能使用该功能' + '\n现在去Metamask官网查看和下载吗？')) {
      location.href = 'https://metamask.io'
    }
    return
  }
  // 检查网络是否为预设网络
  if (store.getters.getNetwork !== store.getters.getPresetNetwork) {
    alert('提醒 您连接的非以太坊主网') // 开发时可以是测试网 发布时需在全局设置中改为主网
    return
  }
  // 检查输入下边界
  if (_sellTokens < store.getters.getSellMinimum) {
    alert('greater than or equal to 1 ss')
    return
  }

  // 检查合约地址
  if (!web3.isAddress(store.getters.getContractAddress)) {
    alert('not get contract address')
    return
  }

  // 检查帐号
  await web3.eth.getCoinbase((er, myAddress) => {
    if (myAddress == null) {
      alert('你还没有登录metamask')
    } else {
      let tokensOfSell = parseInt(web3.toWei(_sellTokens, 'ether'))
      // 检查上边界
      seashells.myTokens({from: myAddress})
        .then((result) => {
          let mybalance = result[0].toString()
          console.log('address: ' + myAddress + ' \nbalance: ' + mybalance + ' \ntokenOfSell: ' + web3.fromWei(tokensOfSell) + ' \nseashells')
          if (tokensOfSell < mybalance) {
            // 卖出事件
            var watchOnTokenSell = seashells.onTokenSell()
            watchOnTokenSell.new()
            watchOnTokenSell.watch(function (error, result) {
              if (!error) {
                var res0 = result.args.customerAddress
                var res1 = web3.fromWei(result.args.tokensBurned)
                var res2 = web3.fromWei(result.args.ethereumEarned)
                console.log('sell result \ncustomerAddress: ' + res0 + ' \ntokensBurned: ' + res1 + ' \nethereumEarned: ' + res2)
              } else {
                myTokens(myAddress)
                myDividends(1, myAddress)
              }
              watchOnTokenSell.uninstall()
            })
            // 卖出
            seashells.sell(tokensOfSell, {from: myAddress},
              function (error, result) {
                if (!error) {
                  alert('from: ' + myAddress + '\nsell value: ' + web3.fromWei(tokensOfSell) + '\nhash:' + result + '\nhash has pending please wait')
                } else {
                  alert('sell' + error)
                }
              })
          } else {
            alert('balance: ' + web3.fromWei(mybalance) + '\nsell: ' + web3.fromWei(tokensOfSell) + '\nsell amount out of you balance')
          }
        })
    }
  })
}

